
ul {
  list-style-type: none;
  padding: 0;
}


.App {
  padding-top: 50px;
  text-align: center;
  background-color: #F0FFF0;
}

img {
  width: 800px;
  border: 2px solid #DAA520;
  border-radius: 5px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;

}
input {
  position: relative;
  margin: 5px;
  width: 55%;
  height: 40px;
  box-shadow: 5px 5px 5px darkgray;
}

input::placeholder{
  color: #DAA520;
  position: absolute;
  transform: translateY(-8px);
  transform: translateX(5px);

}

#submitButton{
  font-weight: bold;
  letter-spacing: 1px;
  margin-right: -450px;
  width: 120px;
  height: 50px;
  background-color: #DAA520;
  color: white;
  border-radius: 5px;
  border: 2px solid darkgray;
  box-shadow: 5px 5px 5px gray;
}
#displayWindow{
  border: 3px solid darkgray;
  box-shadow: 5px 5px 5px gray;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 0 auto;
  text-align: center;
  background-color: #d5e1df;
  width: 75%;
}

p {
  color: #DAA520;
  text-align: left;
  font-weight: 300;
  letter-spacing: 1px;
  margin-left: 10px;
  transform: translateY(30px);
}

#imgBox{
  border-radius: 5px;
  padding-bottom: 20px;
  margin: 0 auto 20px auto;
  width: 803px;
  background-color: white;
  box-shadow: 5px 5px 5px gray;
}
